import styles from './TextAreaBlock.module.scss';
import { FC } from "react"
import { motion } from 'framer-motion'
import Textarea from '@mui/joy/Textarea';
import { ITextAreaBlock } from './TextAreaBlock.intrface';
import { stylesForTextArea } from './stylesForTextArea';

const TextAreaBlock: FC<ITextAreaBlock> = ({ disabled, errors, name, placeholder, register, rules, setError }) => {

    const checkTitleValue = () => setError('text', { message: '  ' })

    return (
        <div
            onClick={checkTitleValue}
            className={styles.main}
        >
            <Textarea
                disabled={disabled}
                placeholder=" ..."
                minRows={8}
                maxRows={10}
                sx={stylesForTextArea}
                {...register('text', {
                    required:
                    {
                        value: true,
                        message: ' '
                    }
                })}
            />

            {/* <motion.div className={styles.error}>
                {errors && <p>{errors}</p>}
            </motion.div> */}

            {/* <div className={styles.tagsBlock}>
            </div> */}

        </div>
    )
};

export default TextAreaBlock;
